import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import ProgressiveImage from 'react-progressive-image'
import { heavy } from '../components/colors'

import Layout from '../components/layout'
import SEO from '../components/seo'
import HeaderImagePlaceFolder from '../components/headerImagePlaceHolder'
import styled from 'styled-components';

import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles({
  root: {
    minWidth: 275,
    marginBottom: 24,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 16,
  },
  footer: {
    paddingLeft: 12,
    paddingRight: 12,
    paddingBottom: 16,
  }
});

const Title = styled.h1`
  color: ${heavy};
  font-size: 22px;
  font-weight: 500;
`;

const CardContainer = styled.div`
  margin-bottom: 32px;
`;

const MoreLinks = styled.a`
  color: ${heavy};
  font-size: 16px;
`;

function PoloDBProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/PoloDB');
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          PoloDB
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
           NoSQL 
        </Typography>
        <Typography variant="body2" component="p">
           SQLite 
          <br />
           MongoDB  API
          
          <br />
           Python/Node.js/C/C++/Swift 
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github </Button>
      </CardActions>
    </Card>
  );
}

function JetpackProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/jetpack.js');
  }

  function article() {
    window.open('https://diverse.space/2020/04/implement-a-extremely-fast-js-bundler')
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          Jetpack.js
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
           JavaScript 
        </Typography>
        <Typography variant="body2" component="p">
          C++ 
          Node.js
          <br />
           ECMAScript 2017
          <br />
           JSX 
          <br />
          
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github </Button>
        <Button size="small" onClick={article}></Button>
      </CardActions>
    </Card>
  );
}

function WebpackProjects() {
  const classes = useStyles();
  
  function more() {
    window.open('https://github.com/vincentdchan/webpack-deep-scope-analysis-plugin');
  }

  function article() {
    window.open('https://diverse.space/2018/05/better-tree-shaking-with-scope-analysis')
  }

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography variant="h5" component="h2">
          Webpack  Plugin
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
           JavaScript  webpack  tree-shaking
        </Typography>
        <Typography variant="body2" component="p">
          GSoC 2018 Google 
          <br />
           Webpack 4  tree-shaking
          <br />
          Webpack 5 
        </Typography>
      </CardContent>
      <CardActions className={classes.footer}>
        <Button size="small" onClick={more}>Github </Button>
        <Button size="small" onClick={article}></Button>
      </CardActions>
    </Card>
  );
}

const IndexPage = () => (
  <StaticQuery
    query={graphql`
    query {
      allMarkdownRemark(
        sort: { order: DESC, fields: [frontmatter___date] }
        limit: 1000
      ) {
        edges {
          node {
            excerpt(pruneLength: 160, truncate: true)
            frontmatter {
              title
              date
              path
              permalink
            }
          }
        }
      }
    }
    `}
    render={data => (
      <Layout
        headerImage={
          <ProgressiveImage src="/images/homepage.jpg" placeholder="">
            {(src, loading) => {
              return loading ? <HeaderImagePlaceFolder /> : (
                <img
                  src={src}
                  alt=""
                  style={{
                    margin: '0px',
                  }}
                />
              );
            }}
          </ProgressiveImage>
        }
      >
        <SEO
          title=""
          keywords={[
            `blog`,
            `technology`,
            `photography`,
          ]}
        />
        <div>
          <Title>  Projects</Title>
        </div>
        <CardContainer>
          <PoloDBProjects />
          <JetpackProjects />
          <WebpackProjects />
        </CardContainer>
        <div>
          <Title>  Interests </Title>
          <ul>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </div>
        <div style={{
          marginBottom: 16,
        }}>
          <Title>  Articles </Title>
          <MoreLinks href="/articles">...</MoreLinks>
        </div>
      </Layout>
    )}
  />
)

export default IndexPage
